:root {
  --duration: 500ms;
  --bg-light: #f9fafb;
  --bg-dark: #18181b;
}
html {
  color-scheme: light;
  font-size: var(--user-font);
  zoom: var(--user-zoom);
  transition: font-size 0.3s ease, zoom 0.3s ease;
}
html.dark {
  color-scheme: dark;
}
@layer base {
  :root {
    --icon-account: url('/account.svg');
    --icon-settings: url('/settings.svg');
  }
  .dark {
    --icon-account: url('/account-dark.svg');
    --icon-settings: url('/settings-dark.svg');
  }
}
.fly-center {
  position: fixed;
  animation: flyCenter 0.5s ease-out forwards;
}
@keyframes flyCenter {
  0% {
    left: var(--x, 0);
    top: var(--y, 0);
    transform: translate(-50%,-50%) scale(0);
  }
  100% {
    left: 50vw;
    top: 50vh;
    transform: translate(-50%,-50%) scale(1);
  }
}
.page-transition {
  animation: fadeIn 0.3s ease-in-out forwards;
}
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
